:root {
  --clr-primary: #333;
  --clr-body: #333;
  --clr-bg: #ddd;
}

body {
  font-family: basic-sans, sans-serif;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.125em;
  line-height: 1.6;
  color: #333;
  color: var(--clr-body);
  background: #ddd;
  background: var(--clr-bg);
}

.content {
  background: white;
  width: 70vw;
  padding: 3em;
  box-shadow: 0 0 3em rgba(0, 0, 0, 0.15);
  position: relative;
}

.search {
  --size: 50px;
  border: 2px solid #333;
  border: 2px solid var(--clr-primary);
  display: flex;
  border-radius: 100px;
  margin-top: 20px;
  margin-left: 49vw !important;
  overflow: hidden;
  font-size: 1.25em;
  position: relative;
  width: var(--size);
  height: var(--size);
  transition: width 450ms cubic-bezier(0.18, 0.89, 0.32, 1.28);
  padding: 3px;
}

.search__input {
  border: 0;
  padding: .25em 1em;
  flex-grow: 1;
  outline: 0;
  z-index: 2;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background: transparent;
  opacity: 0;
  cursor: pointer;
}

.search__submit {
  font-size: 1.5rem;
  margin-left: auto;
  background: 0;
  border: 0;
  cursor: pointer;
  border-radius: 50%;
  transition: background 200ms ease-out;
  width: calc(var(--size) - 10px);
  height: calc(var(--size) - 10px);
  color: #333;
  color: var(--clr-body);
}

.search:focus-within {
  width: 30vw;
}

.search:focus-within .search__input {
  opacity: 1;
  z-index: auto;
  z-index: initial;
  cursor: auto;
  cursor: initial;
  width: calc(100% - var(--size));
}

.search:focus-within .search__submit {
  background: #333;
  background: var(--clr-primary);
  color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}

.search:focus-within .search__submit:hover, .search:focus-within .search__submit:focus {
  outline: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.65);
}

@media (max-width:1025px){
.search{
  margin-left: 48vw !important;
}
.search:focus-within{
  width:40vw;
}
}
