.footer-social-section {
  width: 100%;
  align-items: center;
  justify-content: space-around;
  position: relative;
  margin-top: 5px;
}

.footer-social-section::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 10px;
  border-top: 1px solid #fff;
  width: calc(100% - 20px);
}

.footer-social-overlap {
  position: relative;
  z-index: 2;
  background: #000000;
  padding: 0 20px;
}

.footer-social-connect {
  display: flex;
  align-items: center;
  font: 2.8em Arial;
  /* font-family: Arial, "Oswald", sans-serif; */
