}

.card .imgBx {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    /* z-index: -1; */
}

.card .imgBx img {
    width: 75%;
    height: 100%;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    /* z-index: -1; */
}

.card .imgBx img:hover{
    /* max-width: 70%; */
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    margin: 0 auto;
    /* z-index: -30; */

}

.fa-cart-plus,.fa-heart{
    color: #9b8055;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.fa-cart-plus:hover, .fa-heart:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.productlink{
    text-decoration: none !important;
}
.card .contextBx {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 1;
}

.prod_name{
    margin-bottom: 20px;
}

