    font-size: 18px;
    color: #e35d60;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.card .contextBx .price {
    font-size: 24px;
    color: #000000;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: capitalize;
}

.original_price{
    color: #f38282;
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
}

.card .contextBx .buy {
    position: relative;
    top: 200px;
    opacity: 0;
    padding: 10px 30px;
    margin-top: 15px;
    color: #fff;
    text-decoration: none;
    background: #fff;
    border-radius: 30px;
    -webkit-text-decoration: uppercase;
            text-decoration: uppercase;
    letter-spacing: 1px;
    transition: 0.5s;
}

.card .contextBx  {
    top: 0;
    opacity: 0;
    transition: 0.3s;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
}

.card:hover .contextBx  {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

